.masonry-container{position:relative;display:flex;flex-direction:row;justify-content:space-around;align-items:flex-start;height:auto;padding:var(--masonry-gap);overflow:hidden}.masonry-container.no-padding{padding:0}.masonry-container .masonry__column{display:flex;flex-direction:column;align-items:stretch;justify-content:flex-start;flex-grow:1;max-width:var(--masonry-item-width);min-height:30rem}.masonry-container .masonry__column:not(:last-child){margin-right:var(--masonry-gap)}