.product-card{position:relative;display:inline-flex;flex-direction:column;align-items:center;justify-content:flex-start;width:24rem;min-width:10rem}.product-card .product-card__img-container{position:relative;width:100%;height:max-content;margin-bottom:1.6rem;border:1px solid var(--product-card-img-border-color);padding:.8rem;background-color:var(--product-card-img-bg-color);box-shadow:4px 4px 0 0 var(--product-card-img-shadow-color)}.product-card .product-card__item-summary{position:relative;text-align:center;margin-bottom:1.2rem}.product-card .product-card__item-summary>*{display:block}.product-card .product-card__item-summary .item-summary__name{font-size:var(--size-0);color:var(--product-card-name-text-color);margin-bottom:.4rem}.product-card .product-card__item-summary .item-summary__price{color:var(--text-0);font-size:1.4rem;font-weight:600}.product-card button.item-summary__view-detail-btn{width:100%}